import "./styles.css";

import React, { Component } from "react";
import ReactTable from "react-table-v6";
import "react-table-v6/react-table.css";
//Import Components
import File from "./components/File";

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      posts: []
    };
  }

  componentDidMount() {
    //const url = "https://jsonplaceholder.typicode.com/posts";
    //const url = "https://gist.githubusercontent.com/rominirani/8235702/raw/a50f7c449c41b6dc8eb87d8d393eeff62121b392/employees.json"
    const url = "https://mocki.io/v1/99e1d490-a693-4cad-bb66-2967d3dc34b8";
    fetch(url, {
      method: "GET"
    })
      .then((response) => response.json())
      .then((posts) => {
        this.setState({ posts: posts.Employees });
        console.log("posts", posts.Employees);
      });
  }

  render() {
    //Employee Table Headers/Accessors
    const columns = [
      {
        Header: "Employee_id",
        accessor: "employee_id"
      },
      {
        Header: "Employee_name",
        accessor: "employee_name"
      },
      {
        Header: "Date_of_birth",
        accessor: "date_of_birth"
      },
      {
        Header: "Designation",
        accessor: "designation"
      }
    ];

    return (
      <File>
        <ReactTable columns={columns} data={this.state.posts}></ReactTable>
      </File>
    );
  }
}

export default App;
